* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background-color: #e0e8ff;
  }
  
  main {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    max-width: 500px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    width: 90vw;
    margin-top: 50px;
    padding-bottom: 20px;
  }
  
  h1 {
    background-color: #1b1096;
    color: #fff;
    width: 100%;
    margin-bottom: 10px;
    font-size: 2rem;
    padding: 20px 10px;
  }
  
  .input-section {
    width: 90%;
  }
  
  .input-slider {
    width: 150%;
    margin-top: 5px;
  }
  
  .output {
    padding: 10px 5px;
    margin-top: 10px;
    color: #fff;
    border-radius: 9999px;
    background-color: #1b1096;
    text-align: center; 
    width: 100px;
  }
  
  .output-section {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .conclusion {
    margin-top: 20px;
  }
  